import React, { useState } from "react";
import styles from "./styles.module.css";
import logo from "../assets/cbn.jpg";
import { useSelector, useDispatch } from "react-redux";
import { registerUser } from "../actions/userActions";
import { USER_REGISTER_RESET } from "../constants/userConstants";

const HomeScreen = () => {
  const [firstname, setFirstName] = useState("");
  const [lastname, setLastName] = useState("");
  const [email, setEmail] = useState("");
  const [mobile, setMobile] = useState("");
  const [department, setDepartment] = useState("");
  const [msg, setMsg] = useState("");

  const dispatch = useDispatch();

  const registration = useSelector((state) => state.registration);
  const { loading, error, success } = registration;

  const registerHandler = (e) => {
    e.preventDefault();
    dispatch(registerUser(firstname, lastname, email, mobile, department));
  };

  if (success) {
    setMsg(true);
    dispatch({ type: USER_REGISTER_RESET });
  }
  return (
    <div className={styles.app}>
      <div className={styles.appGrid}>
        <div className={styles.background}>
          <div className={styles.overlay}>
            <h1>Lotus Beta Analytics</h1>
            <p>
              We are focused on the provision of Information Technology
              Solutions and Services that are built on a world-class, secure,
              resilient infrastructure, delivered according to global best
              practices in partnership with the worldâ€™s leading technology
              vendor
            </p>
            <br />
            <h2>Training:</h2>
            <h5>SQL Security</h5>
          </div>
        </div>
        <div className={styles.form}>
          {msg && (
            <div className="alert alert-success">Registration Completed</div>
          )}
          {error && <div className="alert alert-danger">{error}</div>}
          {loading ? (
            <div>Loadding.......</div>
          ) : (
            <>
              <div className="text-center">
                <img src={logo} alt="Logo" width="150" />
              </div>
              <div className={styles.forms}>
                <form onSubmit={registerHandler}>
                  <div className={styles.formContainer}>
                    <label>First Name</label>
                    <input
                      type="text"
                      className="form-control"
                      onChange={(e) => setFirstName(e.target.value)}
                    />
                  </div>
                  <div className={styles.formContainer}>
                    <label>Last Name</label>
                    <input
                      type="text"
                      className="form-control"
                      onChange={(e) => setLastName(e.target.value)}
                    />
                  </div>
                  <div className={styles.formContainer}>
                    <label>Email</label>
                    <input
                      type="email"
                      className="form-control"
                      onChange={(e) => setEmail(e.target.value)}
                      value={email}
                    />
                  </div>
                  <div className={styles.formContainer}>
                    <label>Mobile</label>
                    <input
                      type="number"
                      className="form-control"
                      onChange={(e) => setMobile(e.target.value)}
                    />
                  </div>
                  <div className={styles.formContainer}>
                    <label>Department</label>
                    <input
                      type="text"
                      className="form-control"
                      onChange={(e) => setDepartment(e.target.value)}
                    />
                  </div>
                  <div className={styles.formContainer}>
                    <label style={{ visibility: "hidden" }}>Register</label>
                    <input
                      type="submit"
                      className="form-control btn btn-dark"
                      onChange={(e) => setDepartment(e.target.value)}
                      value="Register"
                    />
                  </div>
                </form>
              </div>
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default HomeScreen;
